.home{
  background: linear-gradient(to right, transparent, $black_shadow), url("background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: relative;
  overflow: hidden;
  &_img{
    height: 220px;
    animation: appear 1s ease-out;
    filter: drop-shadow(0 0 15px rgba(0, 0, 0, 0.5));
  }
  &_title_container{
    color: $white-light;
    text-align: center;
    filter: drop-shadow(0px 5px 10px rgba(0, 0, 0, 1));
    animation: appear 1s ease-out;
  }
  &_title{
    margin: 0;
    font-size: 5rem;
    font-family: $font-viga;
  }
  &_caption{
    margin: 0;
    font-size: 2rem;
    line-height: 3rem;
  }
}

@media (max-width: 915px) {
  .home{
    &_img{
      height: 200px;
    }
  }
}

@media (max-width: 768px) {
  .home{
    background: linear-gradient(to top, transparent, $black_shadow), url("background.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    flex-direction: column-reverse;
    &_img{
      width: calc(100% - 40px);
      height: fit-content;
    }
  }
}

@media (max-width: 425px) {
  .home{
    &_img{
      width: calc(100% + 32px);
      height: fit-content;
    }
    &_title{
      margin: 0;
      font-size: 4rem;
    }
    &_caption{
      font-size: 1.5rem;
    }
  }
}
